
import Vue from 'vue'
import Common from './components/common/api.js'
import {mapGetters} from 'vuex'
import Confirm from 'vux/src/components/confirm/index.vue'
import Loading from 'vux/src/components/loading/index.vue'
import Alert from 'vux/src/components/alert/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import ToastPlugin from 'vux/src/plugins/toast/index.js'


Vue.directive('transfer-dom', TransferDom)
Vue.use(ToastPlugin)
export default {
name: 'App',
data() {
return {
loadText: '加载中..',
}
},
components: {
Confirm,
Loading,
Alert,
XDialog,
},
computed: {
...mapGetters({
transitionName: 'GET_PAGE_TRANSITION',
isLoading: 'GET_LOADING_STATUS',
})
},
mounted() {

},
methods: {

}
};



