import Api from './common/api.js';
import Util from './common/Util.js';
export default {
  name: 'main',
  data () {
    return {
      list: [],
      Util: Util
    }
  },
  mounted(){
    this.getData();
  },
  computed: {

  },
  methods: {
    goBack(){
      this.$router.go(-1);
    },
    getData(){
      Api.get('/news.json', {}, (data) => {
        this.list = data.data;
      }, (err) => {
        console.error(err);
      })
    },
    linkToDetial(item){
      this.$router.push({
        name: 'detial',
        params: {
          item: item
        }
      })
    },
    getDate: Util.getDate,
    strHandle: Util.strHandle,
  }
}



